body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.display-developer {
  color: #000;
  padding: 10px 20px;
}

.display-developer .testArr {
  -webkit-transform: rotateX(60deg) rotate3D(1,1,1,-10deg);
          transform: rotateX(60deg) rotate3D(1,1,1,-10deg);
  height: 120px;
  -webkit-transform-style:preserve-3d;
          transform-style:preserve-3d;
}
.display-developer .testArr canvas {
  opacity: 0.9;
}

.display-developer .horizontal div {
  display: inline-block;
}

.display-developer .horizontal .testArr {
  -webkit-transform: rotateY(238deg) rotate3D(1,1,1,-5deg);
          transform: rotateY(238deg) rotate3D(1,1,1,-5deg);
  height: 120px;
  width: 120px;
  -webkit-transform-style:preserve-3d;
          transform-style:preserve-3d;
}

.display-developer .northsouth .testArr {
  height: 70px;
}

.display-developer .highlight canvas {
  border: 5px solid red;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 24px;
}

html {
  touch-action: manipulation;
}

body{
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

canvas {
  background: #000;
  cursor: pointer;
}

button {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
}

.app {
  width: 1024px;
  height: 1024px;
  margin: 0 auto;
  background: #000;
  color: #ffffff;
  font-size: 17px;
  line-height: 24px;
  text-align: 'left';
  font-family: "Courier New", Courier, monospace;
}

